import React from 'react';
import Types from 'prop-types';

import Option from '../common/Option';

const NavigationOption = ({ icon, title, description, onClick, complex, disabled }) => {
  const sharedProps = { icon, title, description, complex, disabled };

  return (
    <Option
      {...sharedProps}
      onClick={() => {
        if (!disabled) {
          onClick();
        }
      }}
      button={<span className="caret rotate270" />}
    />
  );
};

NavigationOption.propTypes = {
  icon: Types.node.isRequired,
  title: Types.node.isRequired,
  description: Types.node,
  onClick: Types.func.isRequired,
  complex: Types.bool,
  disabled: Types.bool,
};

NavigationOption.defaultProps = {
  description: null,
  complex: false,
  disabled: false,
};

export default NavigationOption;



